* {
    /*background-color: #bd9dea;
    background: linear-gradient(0deg, rgba(225,203,215,1) 0%, rgba(195,142,191,1) 100%);*/
    background-color: #2D2F46;
    font-family: 'PT Sans', sans-serif;
    font-family: 'Gruppo', sans-serif;
}
html{
    scroll-behavior: smooth;
}

h1 {
    font-size: 5rem;
    background-color: transparent;
    font-family: 'Gruppo', sans-serif;
    color: #3BBA9C;
    
}

h2 {
    font-size: 2rem;
    background-color: transparent;
    margin: 2rem 0 0 0;
    color: #707793;
}

h3 {
    font-size: 7rem;
    background-color: transparent;
    color: white;
}

h5 {
    background-color: transparent;
    margin: 0;
    font-family: 'Gruppo', sans-serif;
}

h6 {
    background-color: transparent;
    margin: 0;
    font-size: 4rem;
}

h6 span {
    font-size: 5rem;
    color: #9941b4;
    background-color: transparent;
}

a{
    text-decoration: none;
    color: black;
    font-size: 1rem;
}
p{
    background:transparent;
    color: #ecedee;
}



.headings{
    display: flex;
    justify-content: center;
    margin-bottom: 3rem;
    color: #3BBA9C;
}

.navBar {
    display: flex;
    justify-content: center;
    box-shadow: 0 5px 15px rgb(0, 0, 0);
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #529787;
}

.navBar ul {
    display: flex;
    justify-content: space-between;
    background-color: transparent;
}

.navBar li {
    list-style: none;
    margin: 0 6rem 0 6rem;
    background-color: transparent;

}

.navBar a {
    font-weight: 800;
    background-color: transparent;
    cursor: pointer;
}

.attenDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
}

.footerDiv {
    display: flex;
    justify-content: space-around;
    max-height: 40vh;
    background: rgb(55, 58, 83);
}

.footerDiv ul,li,a {
    background: transparent;
    list-style: none;
    margin-bottom: .5rem;
}

.footerDiv ul {
    align-items: center;
    margin-top: auto;
    margin-bottom: auto;
    padding: 1rem;  
}

.footerDiv a{
    color:#e9e4e4;
    padding: 1rem 1rem 1rem 1rem;
}
.footerDiv h5{
    color:#e9e4e4
}

.footerDiv p{
    font-size:1rem;
    background: transparent;
    color:#ffffff;
}

.footerLogo {
    display: flex;
    align-items: center;
    background-color: transparent;
}

.cgpa-sub-div,.attendex-sub-div,.sgpa-sub-div{
    background: transparent;
}

.attendex-sub-div-spcl{
    display: flex;
    align-items: center;
    background: transparent;
}
.attendex-sub-div-spcl-left{
    background-image: url(../public/blobanimation.svg);
    background-repeat: no-repeat;
    display: flex;
    margin-top: 5rem;
}
.attendex-sub-div-spcl img{
    background: transparent;
    object-fit: cover;
}



.avatarImg {
    object-fit: cover;
    max-width: 10rem;
    max-height: 10rem;
    border-radius: 10rem;
    margin: 2rem;
    border-style: solid;
    padding: 1rem;
    background-color: transparent;
    border-bottom: 0;
    border-width: 1rem;
    border-color:#5841a0;
    
}   

.input {
    padding: 1rem 3rem 1rem 3rem;
    font-size: 1rem;
    border-radius: 4px;
    line-height: 1.5;
    padding: 5px 10px;
    transition: box-shadow 100ms ease-in, border 100ms ease-in, background-color 100ms ease-in;
    border: 2px solid #3BBA9C;
    color: rgb(255, 255, 255);
    background: #43455C;
    display: block;
    height: 30px;
    margin: 0 1rem 0 1rem;
}

.input:hover {
    border-color: #ccc;
}

.input:focus {
    border-color: #9147ff;
    background: #fff;
    color:#2D2F46;
}

.inputCgpa {
    display: block;
}


.form {
    background-color: transparent;
}



#cgpaSelector {
    background-color: #ffffff;
    font-size: 2rem;
    padding: .5rem; 
    margin-bottom: 1rem;
    border-radius: 1rem;
}
.sgpaSelector {
    background: #43455C;
    font-size: 1.35rem;
    padding: .5rem .5rem .5rem .5rem;
    margin: 0 1rem 1rem 1rem;
    border-radius: 1rem;
    color: #ffffff;
    border-style: solid;
    border-color: #3BBA9C;
}

.sgpa-elements{
    background-color: transparent;
    display: flex;
    justify-content: center;
}


.mainDiv {
    display: block;
    padding: 10vh 0 10vh 0;
    border-bottom: solid;
}

.mainDivCgpa {
    display: flex;
    justify-content: space-around;
    background-color: #2D2F46
}
.mainDivSgpa {
    background-color: #2D2F46;
    display: flex;
    justify-content: space-around;

}

.mainDivAttendex {
    background-color: #2D2F46;
    display: flex;
    width: 100%;
    justify-content: space-around;
}

.hero {
    height: 100vh;
    background-image: url(frame.jpeg);
    background-color: #2D2F46;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchBar {
    padding: .5rem 10rem .5rem 3rem;
    font-weight: 100;
    background-color: rgba(255, 255, 255, 0.315);
    border-radius: 3rem;
    font-size: 1.1rem;
    border-style: solid;
    border-color: #e9e4e4;
    color: black;
    opacity: 1;
}

.searchBar:hover {
    background-color: #ffffff;
}

select {
    background-color: #b2ea71;
}




.hero img {
    object-fit: cover;
    width: 100%;
}


.Btn {
    align-items: center;
    appearance: none;
    background: rgb(14,162,126);
    background: radial-gradient(circle, rgba(14,162,126,1) 0%, rgba(0,171,130,1) 100%);
    border: 0;
    border-radius: 6px;
    box-shadow: rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, rgba(58, 65, 111, .5) 0 -3px 0 inset;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    font-family: "JetBrains Mono", monospace;
    height: 48px;
    justify-content: center;
    line-height: 1;
    list-style: none;
    overflow: hidden;
    padding-left: 16px;
    padding-right: 16px;
    position: relative;
    text-align: left;
    text-decoration: none;
    transition: box-shadow .15s, transform .15s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    will-change: box-shadow, transform;
    font-size: 18px;
    margin: 0rem 1rem;
    
}

.sgpa-Btn{
    background: rgb(14,162,126);
    background: radial-gradient(circle, rgba(14,162,126,1) 0%, rgba(0,171,130,1) 100%);
    
}



.App {
    display: block;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}


.hidden{
    display: none;
}


.theme-blue{
    color:#3b76ba;
    border-color: #3b76ba;
}

@media (max-width: 1000px) {

    h1 {
        font-size: 4rem;
    }
    h2{
        font-size: 1rem;
    }

    .attendex-sub-div-spcl{
        display: none;
    }
    .Btn{
        font-size: .8rem;
        padding: .2rem .7rem .2rem .7rem;
    }
    .searchBar{
        padding: .5rem 3rem .5rem 3rem;
    }
    .hero{
        max-height: 100vh;
    }
    .mainDivCgpa{
        align-items: center;
        max-width: 100vw;
        height: 100vh;
        max-height: 100vh;
    }
    .mainDivCgpa h1{
        font-size: 2rem;
    }
    .mainDivCgpa p{
        font-size: .7rem;
    }
    .mainDivAttendex{
        max-height: 100vh;
    }
    .input {
        font-size: 1rem;
        border-radius: .5rem;
        padding: .2rem .5rem;
        display: block;
        height: 30px;
        margin: .2rem 1rem 0 1rem;
    }
    .footerLogo{
        display: none;
    }
    
    .footerDiv{
        align-items: center;
    }
    
}

@media (max-width: 587px) {


    .footerDiv h5{
        font-size: .6rem;
    }
    .footerDiv p{
        font-size: .7rem;
    }

    .avatarImg {
        max-width: 6rem;
        max-height: 8rem;
        margin: 0;
        padding: 0rem;
        border-width: .5rem;
    } 
    .footerDiv a{
        font-size: .6rem;
        padding:0;
    }
    .mainDivSgpa h1{
        font-size: 2rem;

    }





}
